.Input {
  height: 43px;
  width: 326px;
  border: 1px solid #f2f3f5;
  border-radius: 8px;
  background: #f2f3f5;
  position: relative;
  display: flex;
  align-items: center;
  z-index: 5;
}

.Input > input {
  position: absolute;
  width: calc(100% - 26px);
  height: 20px;
  left: 13px;
  bottom: 6px;
  border: none;
  outline: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 19px;
  background: #f2f3f5;
  color: #161616;
  z-index: 5;
  padding: 0;
}

.Input > span {
  position: absolute;
  left: 13px;
  padding: 12px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 6;
  font-weight: 400;
  font-size: 12px;
  line-height: 19px;
  color: #97a1b4;
  background: #f2f3f5;
}

.Input.active {
  border: 1px solid #0762c8;
}

.Input.active span {
  font-size: 10px;
  line-height: 12px;
  top: 7px;
  padding: 0;
}
