@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap");

body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background: #e5e5e5;
  font-family: "Rubik", sans-serif;
}

.main_container {
  width: 1180px;
  margin: 0 auto;
}

.Wrapper {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.Select {
  width: 328px;
  min-height: 45px;
  border-radius: 8px;
  background: #f2f3f5;
  position: relative;
}

.Select .body {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Select .popup {
  position: absolute;
  background: #fff;
  width: 100%;
  padding: 10px 0;
  border-radius: 8px;
  z-index: 6;
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.1);
}

.Select .popup > .element {
  width: calc(100% - 30px);
  padding: 0 15px;
  height: 50px;
  display: flex;
  align-items: center;
  color: #161616;
}

.Select .popup > .element.active {
  background: #c9e8fd;
}

.Select .popup > .element:hover {
  background: rgba(0, 0, 0, 0.04);
}

.Select .name {
  color: #97a1b4;
  font-weight: 400;
  font-size: 12px;
  line-height: 19px;
  padding: 12px 13px;
  white-space: nowrap;
}

.Select .content {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 5px 0;
}

.Select .content > div {
  padding: 2px 7px;
  background: #0762c8;
  color: #fff;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
}

.Footer {
  height: 50px;
  background: #0762c8;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #fff;
  font-weight: 500;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

#root {
  min-height: calc(100vh - 50px);
  padding-bottom: 50px;
  position: relative;
}
