.CalcName {
  width: calc(100% - 40px);
  padding: 20px 20px;
  border-radius: 8px;
  background: #fff;
  color: #161616;
  font-size: 28px;
  line-height: 36px;
  font-weight: 700;
  margin-top: 20px;
}

.Wrapper .button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.Wrapper .button > div {
  background: #0762c8;
  border-radius: 4px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  padding: 12px 20px;
}

.Calc1 {
  display: flex;
  align-items: center;
  background: #fff;
  padding: 20px;
  border-radius: 16px;
  margin-top: 20px;
}

.Calc1 > .Input {
  margin: 0px 20px 0 0;
}

.Calc1 .button {
  margin-top: 0;
}

.Calc2 {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: #fff;
  border-radius: 16px;
  padding-bottom: 20px;
  margin-top: 20px;
}

.Calc2 > div {
  margin: 20px 20px 0 0;
}

.Calc3 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding-bottom: 20px;
  border-radius: 16px;
  background: #fff;
  margin-top: 20px;
}

.Calc3 > div {
  margin: 20px 20px 0 0;
}

.Calc img {
  height: 209px;
  width: 287px;
  padding: 20px 20px 40px 20px;
  border-radius: 8px;
  border: 1px solid #0762c8;
  margin: 20px;
  background: #fff;
}

.Calculators {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Calculators > div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.Calculators > div .Calc {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
}

.Calc > div {
  position: absolute;
  bottom: 30px;
  color: #161616;
}

.answer1 {
  padding: 20px;
  border-radius: 16px;
  background: #fff;
  margin-top: 20px;
  font-size: 18px;
  font-weight: 500;
}

@media (max-width: 1200px) {
  .Wrapper > div {
    width: 1000px;
  }
}

@media (max-width: 1020px) {
  .Wrapper > div {
    width: 800px;
  }
}

@media (max-width: 820px) {
  .Wrapper > div {
    width: calc(100% - 20px);
  }
}
