.Catalog > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.chapter {
  position: relative;
  overflow: hidden;
  margin-top: 25px;
  border-radius: 8px 8px 0 0;
}

.chapter img {
  height: 155px;
  padding-bottom: 50px;
  transition: 1s;
}

.chapter:hover img {
  transform: scale(1.2);
  transition: 1s;
}

.chapter > div {
  position: absolute;
  height: 50px;
  background: #fff;
  width: calc(100% - 30px);
  padding: 0 15px;
  bottom: 4px;
  line-height: 50px;
  color: #161616;
  font-weight: 500;
}

.chapter:hover > div {
  display: flex;
  align-items: center;
  line-height: 26px;
  height: auto;
}
.chapter:hover > div > div {
  padding: 12px 0;
}

.pupup {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pupup .content {
  padding: 30px;
  width: 700px;
  background: #fff;
  border-radius: 16px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.pupup .content img {
  width: 100%;
  margin-bottom: 20px;
  border-radius: 8px;
}

.pupup .content .pro {
  width: calc(50% - 2px);
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  color: #161616;
  font-size: 18px;
  font-weight: 500;
}

.pupup .content .pro:hover {
  background: #e7f4fd;
}

.pupup .content .name {
  color: #0762c8;
  font-size: 32px;
  font-weight: 500;
  margin-bottom: 20px;
}

.Product {
  width: 878px;
  margin: 0 auto;
  background: #fff;
  position: relative;
  margin-top: 20px;
  overflow: hidden;
  border-radius: 16px;
  margin-bottom: 200px;
}

.Product img {
  width: 100%;
}

.Product .name {
  font-size: 30px;
  padding: 45px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  font-weight: 500;
}

.IandB {
  display: flex;
  align-items: center;
  justify-content: center;
}

.IandB .button {
  padding: 12px 20px;
  background: #0762c8;
  border-radius: 4px;
  color: #fff;
  line-height: 19px;
  font-size: 16px;
  margin-left: 50px;
}

/* d */

.table {
  width: 100%;
  display: table;
  margin-top: 30px;
}

.table .table_body {
  display: table-row-group;
}
.table .table_row {
  display: table-row;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #161616;
}
.table .table_cell {
  display: table-cell;
  padding: 10px 15px;
  line-height: 1.42857143;
  vertical-align: middle;
  border-top: 1px solid #e5e5e5;
}

.table .first_row > div {
  border-top: none;
}
/* 
.table .table_cell img {
  height: 80px;
} */

.table .table_row .table_cell:first-child {
  padding-left: 30px;
}
/* 
.table .table_head_row .table_head_cell:first-child {
  padding-left: 30px;
} */

.table .table_row .table_cell:last-child {
  border-left: 1px solid #e5e5e5;
}

.table .table_row:hover {
  background: #e7f4fd;
}

@media (max-width: 1200px) {
  .Catalog > div {
    width: 1000px;
  }
}

@media (max-width: 1020px) {
  .Catalog > div {
    width: 800px;
    justify-content: center;
  }
  .chapter {
    margin-right: 10px;
    margin-left: 10px;
  }
}

@media (max-width: 820px) {
  .Catalog > div {
    width: calc(100% - 20px);
    justify-content: center;
  }
  .pupup .content {
    width: 100%;
  }
  .pupup .content .pro {
    font-size: 16px;
  }
}
