.Header {
  width: 100vw;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #0762c8;
  color: #161616;
  height: 60px;
  position: relative;
  z-index: 7;
}

.HeaderTop {
  height: 25px;
  width: 100vw;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #0762c8;
  border-top: 1px solid #0762c8;
  position: relative;
  z-index: 7;
}

.HeaderTop .flags {
  height: 25px;
  display: flex;
  align-items: center;
}

.HeaderTop .flags img {
  height: 16px;
  margin: 0 5px;
}

.Header > div,
.HeaderTop > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Logo {
  font-size: 34px;
  font-weight: 600;
  line-height: 40px;
  margin: 10px 0;
  position: relative;
  background: #fff;
}

.Logo .menu {
  display: none;
}

.Navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  right: calc(50% - 590px);
  top: 10px;
  z-index: 7;
}

.Navbar > div {
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
}

.Navbar > div:hover {
  text-decoration: underline;
}

.Navbar > div:first-child {
  padding-left: 0;
}

.Navbar > div:last-child {
  padding-right: 0;
}

@media (max-width: 1200px) {
  .Header > div,
  .HeaderTop > div {
    width: 1000px;
  }
  .Navbar {
    right: calc(50% - 500px);
  }
}

@media (max-width: 1020px) {
  .Header > div,
  .HeaderTop > div {
    width: 800px;
  }
  .Navbar {
    right: calc(50% - 400px);
  }
}

@media (max-width: 820px) {
  .Header > div,
  .HeaderTop > div {
    width: calc(100% - 20px);
  }
  .Header > div {
    display: block;
  }
  .HeaderTop > div {
    background: #fff;
  }
  .Navbar {
    width: 100%;
    display: block;
    position: absolute;
    transition: 0.5s;
    background: #fff;
    top: 88px;
    left: 0;
    z-index: 6;
    transform: translateY(-200px);
  }
  .Navbar > div {
    width: 100%;
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #e5e5e5;
  }
  .Logo {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .Logo .menu {
    width: 30px;
    height: 30px;
    display: flex;
    position: relative;
  }
  .menu .line {
    width: 100%;
    height: 4px;
    position: absolute;
    background: #333;
    transition: 0.5s;
  }
  .menu .line:first-child {
    top: 0;
  }
  .menu .line:last-child {
    bottom: 0;
  }
  .menu .line:nth-child(2) {
    top: 13px;
  }
  .menu.active .line:first-child {
    transform: translateY(13px) rotate(-45deg);
    transition: 0.5s;
  }
  .menu.active .line:last-child {
    transform: translateY(-13px) rotate(-135deg);
    transition: 0.5s;
  }
  .menu.active .line:nth-child(2) {
    display: none;
    transition: 0.5s;
  }
  .Navbar.active {
    display: block;
    transform: translateY(0);
    transition: 0.5s;
  }
}
